  calcular(hora: any) {
    if (hora.includes('a')) {
      let parte = hora.split(' ');
      let horaMinutes = parte[0].split(':')
      let horaSeparada = horaMinutes[0];
      let minutosSeparados = horaMinutes[1];

      let nuevaHora = horaSeparada == '12' ? '00' : horaSeparada;

      return nuevaHora + ':' + minutosSeparados

      
      console.log(parte)
    } else if (hora.includes('p')) {
      let parte = hora.split(' ');
      let horaMinutes = parte[0].split(':')
      let horaSeparada = horaMinutes[0];
      let minutosSeparados = horaMinutes[1];

      let nuevaHora = '';

      switch (horaSeparada) {
        case '12':
            nuevaHora = '12'
          break;

          case '01':
            nuevaHora = '13'
          break;

          case '02':
            nuevaHora = '14'
          break;

          case '03':
            nuevaHora = '15'
          break;

          case '04':
            nuevaHora = '16'
          break;

          case '05':
            nuevaHora = '17'
          break;

          case '06':
            nuevaHora = '18'
          break;

          case '07':
            nuevaHora = '19'
          break;

          case '08':
            nuevaHora = '20'
          break;

          case '09':
            nuevaHora = '21'
          break;

          case '10':
            nuevaHora = '22'
          break;


          case '11':
            nuevaHora = '23'
          break;
      
        default:
          break;

      }


      return nuevaHora + ':' + minutosSeparados

      

    }
  }

 async getActivities(data) {
    this.loading.createLoading('Cargando informaciòn, por favor espere')

    this.users = await this.loadUsers() 

    this.locations = await this.loadLocations() 



    console.log(this.locations)


    
    this.activities = [];
    this.cumples = [];
    this.vencis = [];
  
    this.api.getActivities2('31CA0D6B-1A7F-4778-9F5D-07145AFF14FE', data.from, data.to, 'F3B9183F-FA9A-4AF1-BB9E-889C8472D3D4').subscribe(async (data: any) => {
      console.log(data)
      this.calendarRef.nativeElement.getApi().removeAllEvents();
      for (const item of data) {
          const resp: any = await this.getActivityInfo(item.GUID);

          if (resp) {

            const gestion = resp.Values.filter((val) => val.apiId == 'GESTION')
            const desc = resp.Values.filter((val) => val.apiId == 'DESCRIPCION_NOTIFICACION')
            const tiempo = resp.Values.filter((val) => val.apiId == 'TIEMPO')
            const realizado = resp.Values.filter((val) => val.apiId == 'REALIZADO')
            const fechaNotificacion = resp.Values.filter((val) => val.apiId == 'FECHA_NOTIFICACION')
            const horaNotificacion = resp.Values.filter((val) => val.apiId == 'HORA_NOTIFICACION')

            console.log(fechaNotificacion, horaNotificacion, resp.ID)

            

            if (fechaNotificacion.length > 0 && horaNotificacion.length > 0) {
              const hora = this.calcular(horaNotificacion[0].Value)
              if (gestion.length > 0) {
                if (gestion[0].Value != '') {
  
                  resp.gestion = gestion[0].Value;
                  resp.realizado = realizado.length > 0 ? realizado[0].Value : 'NO';
                  resp.clase = this.options.filter((rs) => rs.text == gestion[0].Value).length > 0 ? this.options.filter((rs) => rs.text == gestion[0].Value)[0].class : '' ;
                  resp.desc = desc.length > 0 ? desc[0].Value : '';
                  resp.tiempo = tiempo.length > 0 ? parseFloat(tiempo[0].Value) : 1;
                  resp.fecha = moment(fechaNotificacion[0].Value + ' ' + hora).format('YYYY-MM-DD')
                  resp.programado = moment(fechaNotificacion[0].Value + ' ' + hora).format('YYYY-MM-DD HH:mm')
                  resp.alerta = moment(fechaNotificacion[0].Value + ' ' + hora).subtract(resp.tiempo, 'days').format('YYYY-MM-DD')
                  resp.dur = moment(resp.fecha).diff(moment().format('YYYY-MM-DD'), 'days');
  
                  const miFecha = moment(fechaNotificacion[0].Value + ' ' + hora).format('YYYY-MM-DD HH:mm');

                  console.log(miFecha)
                  if (miFecha >= this.from && miFecha <= this.to) {
                      this.activities.push(resp)
                  }
                 
                }
              }
            }

         

           

          }
      }

      console.log(this.activities)

      this.clientes = _.uniq(_.map(this.activities, (l) => { return l.LocationName}))

      this.comerciales = _.uniq(_.map(this.activities, (l) => { return l.AssignedToName}))

      const clientes = this.locations.filter((cl) => cl.FECHA_CUMPLE);

      const contratos = this.locations.filter((cl) => cl.VENCIMIENTO);

      console.log(clientes, ' mis clientes')

      _.each(clientes, (item, i) => {
        const cumple = moment(item.FECHA_CUMPLE).format('YYYY-MM-DD')

        if (cumple >= this.from && cumple <= this.to) {

          this.cumples.push({
            name: item.Name,
            fecha: cumple
          })

          item.motivo = 'CUMPLE';

          this.calendarRef.nativeElement.getApi().addEvent({
            title: 'Cumpleaños ' + item.Name,
            start: moment(cumple).format('YYYY-MM-DD HH:mm'),
            end:  moment(cumple).add(1,'hours').format('YYYY-MM-DD HH:mm'),
            id: item.Name,
            allDay: true,
            backgroundColor: '#AFFD45',
            textColor: '#000000',
     

            
          }) 
            
        }
     

      })



      _.each(contratos, (item, i) => {
        const venc = moment(item.VENCIMIENTO).format('YYYY-MM-DD')

        if (venc >= this.from && venc <= this.to) {

          this.vencis.push({
            name: item.Name,
            fecha: venc
          })

          item.motivo = 'VENC';

          this.calendarRef.nativeElement.getApi().addEvent({
            title: 'Vencimiento ' + item.Name,
            start: moment(venc).format('YYYY-MM-DD HH:mm'),
            end:  moment(venc).add(1,'hours').format('YYYY-MM-DD HH:mm'),
            id: item.Name,
            allDay: true,
            backgroundColor: '#EC1545',
            textColor: '#ffffff',
     

            
          }) 
            
        }
     

      })





      _.each(this.activities, (item, i) => {
        const gestion = item.Values.filter((val) => val.apiId == 'DESCRIPCION_NOTIFICACION')
        let valor = '';

        if (gestion.length > 0) {
          valor = gestion[0].Value
        }
         this.calendarRef.nativeElement.getApi().addEvent({
              title: valor ? valor : item.LocationName,
              start: moment(item.programado).format('YYYY-MM-DD HH:mm'),
              end:  moment(item.programado).add(1,'hours').format('YYYY-MM-DD HH:mm'),
              id: item.GUID,
              allDay: false,
              
              borderColor: item.realizado == 'SI' ? '#AFFD45' : '',
              backgroundColor: this.getColor(item.gestion),
              textColor: '#ffffff',
       

              
            }) 
      })

      this.loading.cancelLoading()



  

      /* 
      
            */

    }, (err) => {
      this.loading.cancelLoading()
    })
  }

