  escoger(event) {

    console.log(event.detail, this.filtrado)

    if ( event.detail.checked) {

      this.filtrado.push(event.detail.value)

      


    } else {

      this.filtrado = this.filtrado.filter((item) => item != event.detail.value);

    }

    this.filtrar()

   

  }

